.App {
  display: block;
  background-color: #ffffff;
  overflow: hidden;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  
  display: flex;
  flex-direction: column;
  font-size: calc(4px + 1vmin);
  font-family: Didot;
  color: #000000;
  padding: 2%;
}
.Name {
  width: 20%;
  height: 10%;
}
.Navi-bar {
  width: 100%;
  height: 30%;
  padding-top: 2%;
}
.sub-page {
  margin: 5%;
}
.sub-page:first-child {
  margin-right: 20%;
}

.App-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5%;
  padding: 1%;
  line-height: 3vmin;
}
#logo {
  width: 10%;
  height: 10%;
}
.projects {
  width: 70%;
  height: 10%;
  display: flex;
  margin: 5%;
  margin-left: 0%;
  
}
.projects .demo-clip {
  width:20%;
  height:20%;
  border-radius: 50%;
  margin: 2%;
  
}
.projects .demo-text {
  width: 55%;
  padding: 4%;
  line-height: 2.5vmin;
}
.demo-text span {
  display: block;
  font-family: "SF Pro Text",
      "SF Pro Icons",
      "Helvetica Neue",
      "Helvetica",
      "Arial",
      sans-serif;
  margin: 1%;
  font-size: calc(6px + 1vmin);
}
.demo-text .title {
  font-size: calc(6px + 2vmin);
  font-weight: 400;
}
.demo-text .sub-title {
  font-style: italic;
  color:#b1a89b;
}
.brief-intro {
  width:30%
}
.basic-info {
  width: 15%;
}
.basic-info {
  margin: 1%;
}
.intro, .skills {
  width: 65%;
  margin: 2%;
}

.skills .sub-title {
  display: inline-block;
  margin: 1%;
  
}
.basic-info .items {
  width:10%;
  margin:5%;
}
.content {
  font-family: "SF Pro Text",
    "SF Pro Icons",
    "Helvetica Neue",
    "Helvetica",
    "Arial",
    sans-serif;
  font-size: calc(8px + 1vmin);
}
.content, .brief-intro, .basic-info {
  text-align: center;
}
.project-label {
  writing-mode: vertical-rl;
  text-orientation: sideways;
  transform: rotate(-180deg);
  margin-right: 10%;
  text-align: center;
}

.App-link {
  color: #61dafb;
}
a:link {
  color: black;
}

/* visited link */
a:visited {
  color: black;
}




@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
